import Vue from 'vue'
import Router from 'vue-router'
import essay from '../components/essay'
import mainpage from '../components/mainpage'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/mainpage/essay'
    },
    {
      path: '/mainpage',
      component: mainpage,
      children: [
        {
          path: 'essay',
          component: essay
        }
      ]
    }
  ]
})



